      import { PageContent, Markdown } from '../../scripts/components';

      const link = "https://github.com/AngleSharp/anglesharp.github.io/tree/main/docs/blog/2015-10-04-anglesharp-io.md";
      const html = `
<p>In the last days one of the remaining projects was officially launched: <code>AngleSharp.Io</code>. This library will provide many essential IO classes, helper methods, and DOM interfaces. Most importantly it will bring new / improved requesters, such as a much better HTTP/HTTPS requester build on top of the <code>HttpClient</code> class. As a consequence this library will unfortunately not be released as a PCL. In the long run more requesters will be integrated.</p>
<p><code>AngleSharp.Io</code> is also the library that will finally offer a <code>WebSocket</code> implementation. Also the <code>Storage</code> interface will be made available, which can then be instantied as <code>localStorage</code> or <code>sessionStorage</code>. Under the hood the library should also be able to handle caching or resources.</p>
<p>Right now the work has just begun. The first work items only focus on the requester side, with a few interesting items, e.g., <code>WebSocket</code>, being on the way. As with the (JavaScript) scripting library the exact roadmap is unclear at this point in time, however, we will try to come up with one in the next couple of months.</p>
`;
    

